.form_full-width {
    width: 100% !important;
}

.Select-control {
    border-radius: 0;
    background-color: transparent;
    border: none;
}

.Select.is-focused .Select-control {
    background-color: transparent;
}

.Select.is-focused:not(.is-open) > .Select-control {
    border: none;
    -webkit-box-shadow: none;
            box-shadow: none;
    background: transparent;
}


